import React, { useState, useEffect, useRef } from "react";
import { v4 } from "uuid";
import { useAuth } from "../auth/authContext";
import { useHistory } from "react-router-dom";
import "./home.css"
import { Link } from "react-router-dom";
const axios = require('axios')
const { headers } = require("../config")

export default function FindHost() {
    const [link, setlink] = useState("");
    const auth = useAuth();
    const [email, setemail] = useState();
    const history = useHistory();
    const joinmeet = useRef("");
    const [loading, setloading] = useState()
    const [interviewList, setinterviewList] = useState([]);

    const [interview, setinterview] = useState([]);


    useEffect(() => {
        axios.get('https://urban-eatary-backend.herokuapp.com/interview/find', headers).then((res) => {
            var data = res.data.data;
            if (res && data) {
                console.log(data)
                data.forEach(element => setinterviewList((interviewList) => [...interviewList, element]));
            }
            console.log(interviewList)
        })
    }, [])


    useEffect(() => {
        if (localStorage.getItem("jwt") == undefined || auth.user === undefined) {
            setemail("guest");
        } else {
            setemail(auth.user.email);
        }
    }, [auth]);

    const data =
    {
        "typeOfInterview": "Job",
        "numberOfQuestions": 8,
        "levelOfQuestions": "medium",
        "interviewID": link,
        "idOfHost": auth.user.userid
    }

    const route = (e) => {
        e.preventDefault();
        history.push(link);
    };


    const joinMeet = (e) => {
        e.preventDefault();
        history.push(joinmeet.current.value);
    };

    const interviewID = v4();


    const handleLogout = () => {
        auth.logout();
        history.push('/')
    }

    const saveInterviewData = async () => {
        setloading(true)
        try {
            axios.post("https://urban-eatary-backend.herokuapp.com/interview/create", data, headers).then((res) => {
                setinterview(res.data.data);
                setloading(false)
            })
        } catch (err) {
            console.log("Can't Create an Interview" + err)
            setloading(false)
        }
    }


    useEffect(() => {
        saveInterviewData();
    }, [])


    return (
        <div className="homepage">

            <main>
                <div className="box1">
                    <img className="img-hm" src="images/undraw_positive_attitude_re_wu7d.svg"></img>

                    <div className="card" id="col1"
                    >
                        <div className="logout-button">
                            {
                                localStorage.getItem("jwt") ? <button className="pulse" onClick={handleLogout}> logout </button> :
                                    <Link to="/login"><button className="pulse"> Sign In</button></Link>
                            }
                            {
                                localStorage.getItem("jwt") && <button className="pulse" >Account</button>
                            }
                        </div>
                        <div>
                            <div justify="center">
                                <div >
                                    <button

                                        className="raise"
                                        onClick={() => navigator.clipboard.writeText(link)}
                                    >
                                        {link !== ""
                                            ? "http://localhost:3000" + link
                                            : "Create Link"}
                                    </button>
                                    <div className="mb-2">

                                    </div>
                                    <div></div>
                                </div>
                            </div>

                            <div container spacing={2} justifyContent="center">
                                {!loading ? <button
                                    className="raise"
                                    onClick={() => {
                                        setlink(`/setup/${interviewID}`)
                                        saveInterviewData();
                                    }}
                                >
                                    Create Interview
                                </button> : <img src="/images/buffering.png"></img>}
                            </div>
                            <div container spacing={2} justifyContent="center">
                                {!loading && <button
                                    className="raise"
                                    onClick={route}
                                >
                                    Go to interview
                                </button>
                                }
                            </div>
                            {!loading && <div className="bx-jnm" >
                                <input className="inp-hm" ref={joinmeet}></input>
                                <button
                                    className="raise btn-meet"
                                    onClick={joinMeet}
                                >
                                    Join Meet
                                </button>
                            </div>
                            }

                            <Link to={'/setupInterview'}> <button className="offset"> Setup Interview </button>  </Link>
                        </div>
                    </div>

                </div>
                <div>
                    <h1>Find Candidates </h1>
                    {
                        // id: "62651a67f21f90f0bcc4ba31"
                        // interviewID: ""
                        // levelOfQuestions: "medium"
                        // numberOfQuestions: "8"
                        // questions: []
                        // typeOfInterview: "Job"
                        interviewList.length > 0 && interviewList.map((interview) => {
                            return <div className="card-interview"><ul key={interview._id}>
                                <li><h2>Interview ID</h2> {interview.interviewID}</li>
                                <li><h2>Level of Question</h2> {interview.levelOfQuestions}</li>
                                <li><h2>Number of questions</h2> {interview.numberOfQuestions}</li>
                                <li><h2>Type of Interview</h2> {interview.typeOfInterview}</li>
                                <button className="offset">Send Request</button>
                            </ul>
                            </div>
                        })
                    }
                </div>
                <div maxWidth="md"></div>
            </main>
        </div>

    );
}
